	module.exports = {"data":{"markdownRemark":{"html":"<p>An artist focused on fanbase growth is a <em>startup artist</em>.\nThis can be determined by the following question: Would receiving 1,000,000 fans overnight solve most of your problems?\nIf they answer yes, they are a startup artist.</p>\n<p>Like Paul Graham says: \"A barbershop isn't designed to grow fast. Whereas a search engine, for example, is\".\nI got the idea of a startup being differentiated by growth from <a href=\"http://paulgraham.com/growth.html\">this essay</a>.\nThe startup artist, should revolve its strategy around growth checkpoints.\nInstead, I see many measuring consequences/tactics for the growth.</p>\n<p>Here are some better ways to measure (not necessarily accurate numbers, or the only route)</p>\n<table>\n<thead>\n<tr>\n<th>Idea</th>\n<th>As A Consequence Of</th>\n<th>As A Tactic</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>Open for a big band</td>\n<td>show attendance average 100-200</td>\n<td>fans can convince friends to come + 1000 people there will yield maybe 100 new likes</td>\n</tr>\n<tr>\n<td>Get A Budget For An Album</td>\n<td>200 fans giving 10 dollars each in kickstarter campaing</td>\n<td>can promote album at shows, and turn likes into fans</td>\n</tr>\n<tr>\n<td>Get Signed to a record label</td>\n<td>exhausted local fanbase potential</td>\n<td>can venture to other areas, and get promotion which can yield more fans</td>\n</tr>\n</tbody>\n</table>\n<p>Startup artists must focus on rapid growth.\nThe strategies above should only be executed if they answer the following:</p>\n<ol>\n<li>does what we are doing have any growth potential?</li>\n<li>if so, are we doing everything we can to make sure we have that growth?</li>\n<li>is it the least amount of work we can do to get the most new fans?</li>\n</ol>\n<p>If these questions cannot be answered the activity is <a href=\"http://paulgraham.com/before.html\">just playing house</a>.</p>","frontmatter":{"title":"Artists Are Startups"},"fields":{"date":"2016-02-12"}}},"pathContext":{"url":"/posts/2016-02-12-artists-are-startups/"}}

